import React, {PureComponent} from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';

import {userService} from '../../service';
import {localStore, message, monitor, myToast} from '../../utils';
import styles from './login.less';

const phoneReg = /^1\d{10}$/;
const maxNumber = 60;

// 
let defaultAccount = {
    phone: '',
    code: '',
};
/**
 * CODE
 */
const SMG_RESULT_CODE = {
    OK: 'OK', // 
    INVALID_CODE: 'INVALID_CODE', // 
    PHONE_ALEARDY_REGISTED: 'PHONE_ALEARDY_REGISTED', // 
    EXCEED_MAX_COUNT: 'EXCEED_MAX_COUNT', // 
};

/**
 * 
 */
if (process.env.NODE_ENV === 'development') {
    defaultAccount = {
        phone: '18910210381',
        code: '',
    };
}

/**
 * 
 */
class Login extends PureComponent {

    static propTypes = {
        onOk: PropTypes.func, // callback
        onCancel: PropTypes.func,
        grf: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), // id -1
        rf: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), //  rf  -1
    };

    static defaultProps = {
        onOk: () => {
            console.log('');
        },
        onCancel: () => {
            console.log('');
        },
        grf: -1,
        rf: -1
    };

    constructor(props) {
        super(props);
        this.allow = true; // 
        this.state = {
            captchaVisible: false, // 
            countDown: 0,
            phone: defaultAccount.phone,
            code: defaultAccount.code,
            icode: '',
            loadAnimation: false,
        };

    }

    componentDidMount() {

        setTimeout(() => {
            this.setState({
                loadAnimation: true
            });
        }, 200);
    }

    componentWillUnmount() {
        if (this.countTimer) {
            clearInterval(this.countTimer);
        }
    }

    handleInputChange = (key, e) => {
        this.setState({
            [key]: e.target.value
        });
    };

    handleLogin = () => {
        if (!this.allow) {
            return false;
        }
        const {phone, code} = this.state;
        if (!this.verifyLogin(phone, code)) {
            return false;
        }
        const {grf, rf} = this.props;
        this.allow = false;
        userService.login(phone, code, grf, rf)
            .then(({userId, passToken}) => {
                localStore.setToken(userId, passToken);
                this.allow = true;
                monitor.login();
                this.loginSucceed();
            })
            .catch(error => {
                message.error(error.msg || '');
                this.allow = true;
            });
    };

    /**
     * 
     */
    handleSendCode = () => {
        if (!this.allow) return false;
        let {phone, captchaVisible, icode} = this.state;
        if (captchaVisible && !icode) {
            message.info('');
            return false;
        }
        this.setState({loading: true});

        this.allow = false;
        userService.sendMessageCode(phone, icode)
            .then(data => {
                this.allow = true;
                myToast.success('', 1500);
                this.startCountDown();
            })
            .catch(error => {
                this.allow = true;
                let {loading} = this.state;
                loading = false;
                // 
                switch (error.code) {
                    // 
                    case SMG_RESULT_CODE.EXCEED_MAX_COUNT:
                        monitor.log(phone, '');
                        message.error('');
                        break;
                    // 
                    case SMG_RESULT_CODE.PHONE_ALEARDY_REGISTED:
                        monitor.log(phone, '');
                        message.error('');
                        break;
                    // 
                    case SMG_RESULT_CODE.INVALID_CODE:
                        if (captchaVisible) {
                            message.info('');
                        } else {
                            captchaVisible = true;
                        }
                        break;
                    default:
                        message.error(error.msg || '');
                        break;
                }
                this.setState({
                    loading,
                    captchaVisible
                }, this.handleRefreshCode);
            });
    };

    /**
     * 
     */
    startCountDown = () => {
        this.setState({
            loading: false,
            countDown: maxNumber,
        });

        // 
        this.countTimer = setInterval(() => {
            const {countDown} = this.state;
            if (countDown === 1) {
                clearInterval(this.countTimer);
            }
            this.setState({
                countDown: countDown - 1
            });
        }, 1000);
    };

    /**
     * 
     */
    loginSucceed = () => {
        const {onOk} = this.props;
        message.info('');
        this.setState({
            loadAnimation: false
        });
        setTimeout(() => {
            onOk && onOk();
        }, 300);

    };

    /**
     * 
     */
    handleCancel = () => {
        const {onCancel} = this.props;
        this.setState({
            loadAnimation: false
        });
        setTimeout(() => {
            onCancel && onCancel();
        }, 300);
    }

    /**
     * 
     */
    verifyLogin = (phone, code) => {
        if (!phone) {
            message.info('');
            return false;
        }
        if (!phoneReg.test(phone)) {
            message.info('');
            return false;
        }
        if (!code) {
            message.info('');
            return false;
        }
        if (code.length < 4) {
            message.info('');
            return false;
        }
        return true;
    };

    /**
     * 
     */
    handleRefreshCode = () => {
        const {captchaVisible} = this.state;
        if (captchaVisible) {
            this.refs.captchaImg.src = '/captcha/get?t=' + Math.random();
        }
    };

    /**
     * render
     */
    renderSendMsgBtn = () => {
        const {phone, countDown} = this.state;
        let sendMsgText = countDown < 1 ? '' : '';
        if (phoneReg.test(phone) && countDown < 1) {
            return (
                <div className={classNames(styles.sendMsgBtn, styles.active)}
                    onClick={this.handleSendCode}>{sendMsgText}</div>
            );
        }
        return <div className={styles.sendMsgBtn}>{countDown ? (countDown + sendMsgText) : sendMsgText}</div>;
    };

    /**
     * render
     */
    renderCaptchaCode = () => {
        let {icode, captchaVisible} = this.state;
        if (!captchaVisible) return null;
        return (
            <div className={classNames(styles.setItem, styles.icodeWrap, 'clearfix')}>
                <label/>
                <input
                    type="text" maxLength="4" value={icode}
                    onChange={this.handleInputChange.bind(null, 'icode')} placeholder=""
                    autoComplete="off"
                />
                <div className={styles['captchaImg']} onClick={this.handleRefreshCode}>
                    <img src={''} ref="captchaImg" alt=""/>
                </div>
            </div>
        );
    }

    render() {
        const {phone, code, loadAnimation} = this.state;
        return (
            <div className={classNames(styles.loginWrapper, loadAnimation ? styles.animation : '')}>
                <div className={styles.content}>
                    <div className={styles.section}>
                        <div className={classNames(styles.setItem, styles.phoneWarp, 'clearfix')}>
                            <label/>
                            <input
                                type="tel"
                                maxLength="11"
                                value={phone}
                                onChange={this.handleInputChange.bind(null, 'phone')}
                                placeholder=""
                                autoComplete="off"
                            />
                        </div>
                        {this.renderCaptchaCode()}
                        <div className={classNames(styles.setItem, styles.codeWrap, 'clearfix')}>
                            <label/>
                            <input
                                type="text" maxLength="4" value={code} pattern="[0-9]*"
                                onChange={this.handleInputChange.bind(null, 'code')} placeholder=""
                                autoComplete="off"
                            />
                            {this.renderSendMsgBtn()}
                        </div>
                    </div>
                    <div className={styles.function}>
                        <div className={styles.closeBtn} onClick={this.handleCancel}></div>
                        <div className={styles.loginBtn} onClick={this.handleLogin}></div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Login;
