import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { tools } from '../../utils';
import * as styles from './video.less';
import playImgSrc from '../../public/images/bunny/video_play.png';

let count = 0;

// const defaultSrc = 'https://resource-staging.mmears.com/market/video/3CE3BBD9-B6DF-483E-9A2A-D14D2C935CDE_20190505160841634.MOV';

/**
 * , H5 IOS
 */
class VideoPlayer extends PureComponent {
  static propTypes = {
    width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    className: PropTypes.string,
    preload: PropTypes.string,
    poster: PropTypes.string, // 
    src: PropTypes.string, // 
    onPlay: PropTypes.func, // : 
    onPlaying: PropTypes.func, // 
    onPause: PropTypes.func, // : 
    onEnd: PropTypes.func // : 
  };

  static defaultProps = {
    width: 300, //  3:4
    height: 400, // IOSandroid 
    className: '',
    preload: 'metadata', // auto:     metadata:      none: 
    poster: '',
    src: '',
    onPlay: () => {},
    onPlaying: () => {},
    onPause: () => {},
    onEnd: () => {}
  };

  constructor(props) {
    super(props);

    this.isIOS = tools.isIOS(); // iOS
    this.isAndroid = tools.isAndroid(); // Android
    this.status = ''; // , reset , play , / 
    this.state = {
      playImage: 'show',
      playId: `mmears-box-${count++}` // Android
    };
  }

  componentDidMount() {
    this.init();
  }

  componentWillUnmount() {
    clearTimeout(this.playTimer);
  }

  player = null;

  playTimer = null;

  /**
   * 
   * currentTime, duration
   */
  init = () => {
    if (this.isIOS) {
      this.player.setAttribute('playsinline', true); //  iPhone Safari 
      this.player.setAttribute('webkit-playsinline', true); //  iPhone Safari 
    } else {
      this.player.setAttribute('x5-playsinline', true);
    }
  };

  /**
   *  
   */
  handleImagePlay = () => {
    this.setState({
      playImage: false
    });
    this.player.play();
  };

  /**
   * 
   */
  handlePlay = () => {
    const { onPlay } = this.props;
    onPlay && onPlay();

    // console.log('handlePlay >> status=play');
    this.status = 'play';
    // console.log('this.status', this.status);
  };

  /**
   * 
   */
  handlePlaying = () => {
    const { onPlaying } = this.props;
    onPlaying && onPlaying();
  };

  /**
   * 
   */
  handlePause = () => {
    const { onPause } = this.props;
    onPause && onPause();

    // console.log('', this.player);
  };

  /**
   * 
   */
  handleEnded = () => {
    const { onEnd } = this.props;
    if (this.isAndroid) {
      // 
      this.player.play();
      // , 
      this.playTimer = setTimeout(() => {
        this.player.pause();
      }, 60);
    }

    // console.log('', this.player);

    onEnd && onEnd();
  };

  /**
   * Android
   */
  resetPlayer = () => {
    // IOSz-index, return
    if (this.isIOS) return;

    // , , key

    //    , 
    if (!this.status || this.status === 'reset') return;

    // console.log('resetPlayer >> status=reset');
    this.status = 'reset';
    // console.log('this.status', this.status);
    this.setState({
      playImage: 'show',
      playId: `mmears-box-${count++}`
    });
  };

  /**
   * IOS
   * 
   * @returns {*}
   */
  renderIOSContent = () => {
    const { width, src, poster, className, preload } = this.props;

    const videoBoxClassName = classNames(styles.videoBox, className);

    return (
      <div
        className={videoBoxClassName}
        ref={(ele) => {
          this.videoPlayer = ele;
        }}
      >
        <video
          ref={(player) => {
            this.player = player;
          }}
          width={width}
          controls={true}
          src={src}
          poster={poster}
          preload={preload}
          className={className}
          onPlay={this.handlePlay}
          onPause={this.handlePause}
          onPlaying={this.handlePlaying}
          onEnded={this.handleEnded}
        >
          video
        </video>
      </div>
    );
  };

  /**
   * 
   * 
   * @returns {*}
   */
  renderAndroidContent = () => {
    const { playId, playImage } = this.state;

    const { width, src, poster, className, preload } = this.props;

    const videoBoxClassName = classNames(styles.videoBox, className);

    return (
      <div
        className={videoBoxClassName}
        ref={(ele) => {
          this.videoPlayer = ele;
        }}
      >
        {playImage === 'show' ? <img src={playImgSrc} onClick={this.handleImagePlay} /> : null}
        <video
          key={playId}
          ref={(player) => {
            this.player = player;
          }}
          controls={false}
          src={src}
          width={width}
          poster={poster}
          preload={preload}
          className={className}
          onPlay={this.handlePlay}
          onPause={this.handlePause}
          onPlaying={this.handlePlaying}
          onEnded={this.handleEnded}
        >
          video
        </video>
      </div>
    );
  };

  render() {
    if (this.isIOS) {
      return this.renderIOSContent();
    }

    return this.renderAndroidContent();
  }
}

export default VideoPlayer;
