import React, { Component } from 'react';
import { InputItem } from 'antd-mobile';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import queryString from 'query-string';

import { message, monitor, localStore, myToast } from '../../utils';
import { userService } from '../../service';
import BaseModal from '../modal/base-modal';
import styles from './modal-login.less';
import loginLogo from '../../public/images/common/login-logo.png'; // bonney
import RabbitLoginLogo from '../../public/images/rabbit/rabbitHeaderLogo.png'; // 

const phoneReg = /^1\d{10}$/;
const maxNumber = 60;

// 
let defaultAccount = {
  phone: '',
  code: ''
};
/**
 * CODE
 */
const SMG_RESULT_CODE = {
  OK: 'OK', // 
  INVALID_CODE: 'INVALID_CODE', // 
  PHONE_ALEARDY_REGISTED: 'PHONE_ALEARDY_REGISTED', // 
  EXCEED_MAX_COUNT: 'EXCEED_MAX_COUNT' // 
};

/**
 *  
 */
const ACT_TYPE_CODE = {
  BARGAIN_ACTIVITY: 'BARGAIN_ACTIVITY', // 
  LUCKY_DRAW: 'LUCKY_DRAW', // 
  GROUP_BUYING_SP: 'GROUP_BUYING_SP', // 
  PROMOTION_PAGE: 'PROMOTION_PAGE', // 
  LOW_PRICE_COURSE: 'LOW_PRICE_COURSE', // 
  SP_EXCHANGE: 'SP_EXCHANGE', // 
  GROUP_BUYING: 'GROUP_BUYING' // 
};

/**
 * 
 */
if (process.env.NODE_ENV === 'development') {
  defaultAccount = {
    phone: '18910210381',
    code: ''
  };
}

export default class ModalLogin extends Component {
  static propTypes = {
    onOk: PropTypes.func, // callback
    onCancel: PropTypes.func,
    promotion: PropTypes.string, // / 
    teamLeaderId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), // id
    actId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]), //  id
    actType: PropTypes.string, //  
    closeBtn: PropTypes.bool,
    source: PropTypes.string // , 
  };

  static defaultProps = {
    /**
     * or
     * @param operateType: REGISTER , LOGIN 
     */
    onOk: (operateType) => {
      console.log('');
    },
    onCancel: () => {
      console.log('');
    },
    promotion: '',
    teamLeaderId: '', // id
    actId: '', //  id
    actType: '', //  
    closeBtn: true,
    source: '' // , 
  };

  constructor(props) {
    super(props);
    this.allow = true; // 

    this.captchaImg = React.createRef();

    this.urlParams = queryString.parse(window.location.search);

    this.state = {
      captchaVisible: false, // 
      countDown: 0,
      phone: defaultAccount.phone,
      code: defaultAccount.code,
      icode: ''
    };
  }

  componentWillUnmount() {
    if (this.countTimer) {
      clearInterval(this.countTimer);
    }
  }

  handleInputChange = (key, value) => {
    this.setState({
      [key]: value
    });
  };

  /**
   * 
   */
  handleLogin = () => {
    if (!this.allow) {
      return false;
    }
    const { phone, code } = this.state;
    if (!this.verifyLogin(phone, code)) {
      return false;
    }

    const { promotion, actId, actType, source } = this.props;
    //  teamLeaderId
    let teamLeaderId;
    switch (promotion) {
      case 'group': // 
        // idId-1
        teamLeaderId = this.props.teamLeaderId || -1;
        break;
    }

    //  
    let activityType;
    switch (actType) {
      case 'landing':
        activityType = ACT_TYPE_CODE.LOW_PRICE_COURSE;
        break;
      case 'group':
        activityType = ACT_TYPE_CODE.GROUP_BUYING;
        break;
      default:
        activityType = actType;
        break;
    }

    const rf = this.urlParams.rf || -1;
    this.allow = false;
    userService
      .login(phone, code, rf, teamLeaderId, actId, activityType, source)
      .then(({ userId, passToken, operateType }) => {
        localStore.setToken(userId, passToken);
        if (operateType) {
          localStore.set('operateType', operateType);
        }
        monitor.log(phone, '/');
        this.allow = true;
        monitor.login(userId);
        this.loginSucceed(operateType);
      })
      .catch((error) => {
        message.error(error.msg || '');
        monitor.log(phone, '/');
        this.allow = true;
      });
  };

  /**
   * 
   */
  handleSendCode = () => {
    if (!this.allow) return false;
    const { phone, icode } = this.state;
    let { captchaVisible } = this.state;
    const { source } = this.props;
    if (captchaVisible && !icode) {
      message.info('');
      return false;
    }

    monitor.log(phone, '');
    this.setState({ loading: true });

    this.allow = false;
    userService
      .sendMessageCode(phone, icode, source)
      .then((data) => {
        this.allow = true;
        myToast.success('', 1500);
        monitor.log(phone, '');
        this.startCountDown();
      })
      .catch((error) => {
        this.allow = true;
        let { loading } = this.state;
        loading = false;
        // 
        switch (error.code) {
          // 
          case SMG_RESULT_CODE.EXCEED_MAX_COUNT:
            monitor.log(phone, '');
            message.error('');
            break;
          // 
          case SMG_RESULT_CODE.PHONE_ALEARDY_REGISTED:
            monitor.log(phone, '');
            message.error('');
            break;
          // 
          case SMG_RESULT_CODE.INVALID_CODE:
            monitor.log(phone, '');
            if (captchaVisible) {
              message.info('');
            } else {
              captchaVisible = true;
            }
            break;
          default:
            message.error(error.msg || '');
            monitor.log(phone, '');
            break;
        }
        this.setState(
          {
            loading,
            captchaVisible
          },
          this.handleRefreshCode
        );
      });
  };

  /**
   * 
   */
  startCountDown = () => {
    this.setState({
      loading: false,
      countDown: maxNumber
    });

    // 
    this.countTimer = setInterval(() => {
      const { countDown } = this.state;
      if (countDown === 1) {
        clearInterval(this.countTimer);
      }
      this.setState({
        countDown: countDown - 1
      });
    }, 1000);
  };

  /**
   * 
   * @param operateType: REGISTER , LOGIN 
   */
  loginSucceed = (operateType) => {
    const { onOk } = this.props;
    message.info('');
    onOk && onOk(operateType);
  };

  /**
   * 
   */
  handleCancel = () => {
    const { onCancel } = this.props;
    onCancel && onCancel();
  };

  /**
   * 
   */
  verifyLogin = (phone, code) => {
    if (!phone) {
      message.info('');
      return false;
    }
    if (!phoneReg.test(phone)) {
      message.info('');
      return false;
    }
    if (!code) {
      message.info('');
      return false;
    }
    if (code.length < 4) {
      message.info('');
      return false;
    }
    return true;
  };

  /**
   * 
   */
  handleRefreshCode = () => {
    const { captchaVisible } = this.state;
    if (captchaVisible) {
      this.captchaImg.current.src = '/captcha/get?t=' + Math.random();
    }
  };

  /**
   * render
   */
  renderSendMsgBtn = () => {
    const { phone, countDown } = this.state;
    const sendMsgText = countDown < 1 ? '' : '';
    if (phoneReg.test(phone) && countDown < 1) {
      return (
        <div className={classNames(styles.sendMsgBtn, styles.active)} onClick={this.handleSendCode}>
          {sendMsgText}
        </div>
      );
    }
    return (
      <div className={styles.sendMsgBtn}>{countDown ? countDown + sendMsgText : sendMsgText}</div>
    );
  };

  /**
   * render
   */
  renderCaptchaCode = () => {
    const { icode, captchaVisible } = this.state;
    if (!captchaVisible) return null;
    return (
      <div className={classNames(styles['login-form-item'], 'clearfix')}>
        <InputItem
          placeholder=""
          moneyKeyboardAlign="left"
          maxLength="4"
          autoComplete="off"
          className={styles['captcha-input']}
          onChange={this.handleInputChange.bind(this, 'icode')}
          value={icode}
        />
        <div className={styles['captchaImg']} onClick={this.handleRefreshCode}>
          <img src="" ref={this.captchaImg} alt="" />
        </div>
      </div>
    );
  };

  render() {
    const { phone, code } = this.state;
    const { hearderBg, closeBtn } = this.props;
    const headerSrc = hearderBg === 'rabbit' ? RabbitLoginLogo : loginLogo;
    return (
      <BaseModal
        closable={closeBtn}
        modalType="edit"
        onCancel={this.handleCancel}
        header={
          <div className={styles[hearderBg === 'rabbit' ? 'rabbit-login-header' : 'login-header']}>
            <img src={headerSrc} alt="" />
          </div>
        }
        footer={
          <div className={styles['login-footer-item']}>
            <button onClick={this.handleLogin}></button>
          </div>
        }
      >
        <div className={styles['login-form']}>
          <div className={styles['login-form-item']}>
            <InputItem
              type="number"
              placeholder=""
              maxLength="11"
              moneyKeyboardAlign="left"
              className={styles['login-input']}
              onChange={this.handleInputChange.bind(this, 'phone')}
              value={phone}
            />
          </div>
          {this.renderCaptchaCode()}

          <div className={styles['login-form-item']}>
            <InputItem
              type="number"
              placeholder=""
              maxLength="4"
              moneyKeyboardAlign="left"
              className={styles['code-input']}
              onChange={this.handleInputChange.bind(this, 'code')}
              value={code}
            />
            {this.renderSendMsgBtn()}
          </div>
        </div>
      </BaseModal>
    );
  }
}
