// >>>:
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import classNames from 'classnames';

//     >>>:
import { appConfig } from '../../config';
import { logger } from '../../utils/monitor';
import { RabbitPayBtn } from '../component';
import { ModalLogin, BaseModal } from '../../components';
import { ACTIVITY_COUPON_STATUS, ACTIVITY_PROCESS_STATUS } from '../../constants';

// >>:
import styles from './eleven.less';

/**
 * : id1
 */
const activityId = 1;

/**
 * 
 */
const { productListUrl } = appConfig.resources;

/**
 * : 
 */
@inject(({ elevenStore }) => {
  return {
    // 
    productId: elevenStore.state.productId, // id
    modal: elevenStore.modal,
    depositRecords: elevenStore.state.depositRecords, // 
    activityStatus: elevenStore.state.activityStatus, // 
    // 
    initData: elevenStore.initData, // 
    buyingBtn: elevenStore.buyingBtn, // 
    changeModalValue: elevenStore.changeModalValue // key
  };
})
@observer
export default class HasParticipate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mask: false
    };
  }

  componentDidMount() {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
  }

  /**
   * 
   */
  handlePreCheck = () => {
    return this.props.buyingBtn();
  };

  /**
   * /
   */
  handleLoginCallback = () => {
    this.handleHideModal();
    // 
    this.refByPayBtn.current.handleClick();
  };

  /**
   * modal
   */
  handleHideModal = () => {
    this.props.changeModalValue('');
  };

  /**
   * 
   */
  renderShareBlock = () => {
    const { mask } = this.state;
    if (mask) {
      return (
        <div className={styles.mask} onClick={this.handleClickByCloseShare}>
          <span className={styles.icon}></span>
        </div>
      );
    }
    return null;
  };

  /**
   * 
   */
  handleClickByOpenShare = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
    this.setState({
      mask: true
    });
    // 
    const scrY = window.scrollY;
    this.scroH = scrY;
    document.body.style.position = 'fixed';
    document.body.style.top = -scrY + 'px';
  };

  /**
   * 
   */
  handleClickByCloseShare = () => {
    this.setState({
      mask: false
    });
    // 
    document.body.style.position = '';
    window.scrollTo(0, this.scroH);
  };

  /**
   * render
   * login:  
   */
  renderModal = () => {
    const { modal } = this.props;
    switch (modal) {
      case 'login':
        return (
          <ModalLogin
            actId={activityId}
            actType="DEPOSIT"
            onOk={this.handleLoginCallback}
            onCancel={this.handleHideModal}
          />
        );
      case 'rules':
        return (
          <BaseModal
            header={
              <div className={styles['info-header']}>
                <div className={styles['header-bg']}></div>
              </div>
            }
            closable={true}
            onCancel={this.handleHideModal}
          >
            <div className={styles.rules}>
              <div className={styles.content}></div>
            </div>
          </BaseModal>
        );
      default:
        return null;
    }
  };

  /**
   * 
   */
  successReplace = (num) => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
    this.props.initData(activityId);
  };

  /**
   * 
   */
  payOnCancel = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
    this.props.initData(activityId);
  };

  /**
   * 
   */
  payOnFail = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
    this.props.initData(activityId);
  };

  /**
   * 
   */
  goProductList = () => {
    logger.info({
      eventCode: 'ELEVEN_COUPON',
      message: ''
    });
    window.location.href = productListUrl;
  };

  /**
   * 
   */
  renderCouponList = () => {
    const { depositRecords } = this.props;
    let couponList = null;
    if (depositRecords.length) {
      couponList = (
        <div className={styles.coupon}>
          {depositRecords.map((item, index) => {
            return (
              <div key={`coupon-${index}`} className={styles.list}>
                <div className={styles.tip}>
                  <p className={styles.font1}>1</p>
                  <p className={styles.font2}>2019.11.11</p>
                </div>
                {item.couponStatus === ACTIVITY_COUPON_STATUS.UNUSED && (
                  <div
                    className={classNames(styles.btn, styles.immediateUse)}
                    onClick={this.goProductList}
                  >
                    
                  </div>
                )}
                {(item.couponStatus === ACTIVITY_COUPON_STATUS.USED ||
                  item.couponStatus === ACTIVITY_COUPON_STATUS.FREEZE) && (
                  <div className={classNames(styles.btn, styles.alreadyUsed)}></div>
                )}
                {item.couponStatus === ACTIVITY_COUPON_STATUS.OBSOLETE && (
                  <div className={classNames(styles.btn, styles.alreadyUsed)}></div>
                )}
              </div>
            );
          })}
        </div>
      );
    }
    return couponList;
  };

  /**
   * 
   */
  activityRules = () => {
    this.props.changeModalValue('rules');
  };

  render() {
    const { productId, activityStatus } = this.props;
    // activityStatus 
    return (
      <div className={styles.hasParticipate}>
        <div className={styles.header}>
          <div className={styles.rule} onClick={this.activityRules}>
            
          </div>
        </div>
        {this.renderCouponList()}
        <div className={styles.share}>
          <div className={styles.title}></div>
          <div className={styles.box}></div>
          <p className={styles.title1}>1</p>
          <p className={styles.title2}>~</p>
          <div className={styles.shareBtn} onClick={this.handleClickByOpenShare}>
            
          </div>
        </div>
        {this.renderShareBlock()}
        {activityStatus === ACTIVITY_PROCESS_STATUS.ACTIVITY_PROCESSING ? (
          <RabbitPayBtn
            ref={this.refByPayBtn}
            productId={productId}
            preCheck={this.handlePreCheck}
            onSuccess={this.successReplace}
            onCancel={this.payOnCancel}
            onFail={this.payOnFail}
          >
            <div className={classNames(styles.footer, styles.cancelFooter)}>
              <p></p>
              <p>11000</p>
            </div>
          </RabbitPayBtn>
        ) : null}
        {this.renderModal()}
      </div>
    );
  }
}
