// >>>:
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import * as tools from '@mmears/mmears-tools';

//     >>>:
import { shareConfig } from '../../config';
import { logger } from '../../utils/monitor';
import { AccountsType, ACTIVITY_BUYING_STATUS } from '../../constants';
import { DocumentTitle, WechatConfig, checkUserAuth } from '../../components';

//  >>>:
import { ServiceError } from '../common';
import NoParticipate from './no-participate';
import HasParticipate from './has-participate';

// 
const defaultShareOptions = shareConfig.eleven;

/**
 * : id1
 */
const activityId = 1;

/**
 * 
 */
@inject(({ elevenStore }) => {
  return {
    // 
    buyFlag: elevenStore.state.buyFlag, // 
    userId: elevenStore.state.userId, // userId
    serviceError: elevenStore.state.serviceError, // 
    errorMsg: elevenStore.state.errorMsg, // 
    // 
    initData: elevenStore.initData
  };
})
@observer
@checkUserAuth({
  account: AccountsType.MAIN,
  support: 'wechat'
}) // --
export default class Eleven extends Component {
  constructor(props) {
    super(props);
    this.props.initData(activityId);
  }

  componentDidMount() {
    const originAddress = tools.getQueryString('origin');
    /**
     * fromAddress 
     * article: 
     * menu: H5
     * share: 
     * order: 11
     */
    switch (originAddress) {
      case 'article':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: ''
        });
        return;
      case 'menu':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: 'H5'
        });
        return;
      case 'share':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: ''
        });
        return;
      case 'order':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: '11'
        });
        return;
      case 'channel':
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: ''
        });
        return;
      default:
        logger.info({
          eventCode: 'ELEVEN_COUPON',
          message: ''
        });
        return;
    }
  }

  /**
   * 
   */
  renderPageByBuy = () => {
    const { buyFlag } = this.props;
    switch (buyFlag) {
      case ACTIVITY_BUYING_STATUS.NO:
        return <NoParticipate />;
      case ACTIVITY_BUYING_STATUS.YES:
        return <HasParticipate />;
      default:
        return null;
    }
  };

  /**
   * 
   * , rfurlrf=userId
   */
  shareParam = () => {
    const rfCode = Number(tools.getQueryString('rf')) !== -1 && tools.getQueryString('rf');
    const { userId } = this.props;
    const shareConfig = {
      ...defaultShareOptions,
      link:
        rfCode && userId
          ? `${window.location.protocol}//${window.location.host}${window.location.pathname}?origin=share&rf=${userId}`
          : `${window.location.protocol}//${window.location.host}${window.location.pathname}?origin=share&rf=${rfCode}`
    };
    return shareConfig;
  };

  render() {
    const { serviceError, errorMsg, userId } = this.props;
    // , 
    if (serviceError) {
      return <ServiceError error={errorMsg} />;
    }

    const rfCode = Number(tools.getQueryString('rf')) !== -1 && tools.getQueryString('rf');
    const wechatConfigKey = rfCode + userId; // keyWechatConfig/2WechatConfig
    return (
      <>
        {this.renderPageByBuy()}
        <DocumentTitle title="" />
        <WechatConfig
          key={wechatConfigKey}
          showShare={true}
          shareOptions={this.shareParam()}
          account={AccountsType.MAIN}
        />
        {/* {rfCode && userId && (
          <WechatConfig
            showShare={true}
            shareOptions={this.shareParam()}
            account={AccountsType.MAIN}
          />
        )} */}
      </>
    );
  }
}
